.DisplayMarkdown {
    flex: 1 0 80%;
}

.row {
    width: calc(100% - 80px);
    margin: auto;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 10px;
    grid-column-gap: 40px;
}

.save-button {
    padding: 1em;
}

input[type='button'] {
  width: 78px;
  display: block;
  font-size: 1em;
  font-weight: bold;
  border: 1px solid #000;
  border-radius: 0.5em;
  outline: none;
}

input[type='button']:hover {
  color: #000;
  background-color: #fff;
  outline: none;
}

.long {
    height: 75vh;
}

.inner-header {
    text-align: left;
}

.editor > textarea {
  flex: 1;
}

textarea {
    border-color: rgb(38, 38, 69);
    width: 100%;
    height: 100%;
}

.preview {
    border-top: 1px solid rgb(38, 38, 69);
    border-radius: 10px;
    border-color:  rgb(38, 38, 69);
    text-align: left;
}